<template>
  <div id="app">
    <div class="container" id="container">
      <div class="col-lg-6">
        <div class="form-container sign-up-container ">
          <form @submit.prevent="validateForm" action="/">
            <h1>Create Account</h1>
            <div class="social-media">
              <a href=""><i class="fab fa-facebook-f"></i></a>
              <a href=""><i class="fab fa-google-plus-g"></i></a>
              <a href=""><i class="fab fa-linkedin-in"></i></a>
            </div>
            <p>or use your email for registration</p>
            <input
              type="text"
              placeholder="Name"
              name="name"
              v-model="nameVal"
              id="name"
            />
            <p class="error">{{ errorMessage }}</p>
            <input
              type="email"
              placeholder="Email"
              name="email"
              v-model="emailVal"
              id="email"
            />
            <p class="error">{{ errorMessageEmail }}</p>
            <input
              type="text"
              class="password"
              autocomplete="off"
              placeholder="Password"
              name="password"
              v-model="passVal"
              id="pass"
            />
            <p class="error">{{ errorMessagePass }}</p>
            <button type="submit">Sign Up</button>
          </form>
        </div>
        <div class="form-container sign-in-container">
          <form
            @submit.prevent="validateSignInForm"
            action="/"
            method="get"
            name="RegForm"
          >
            <h1>Sign In</h1>
            <div class="social-media">
              <a href=""><i class="fab fa-facebook-f"></i></a>
              <a href=""><i class="fab fa-google-plus-g"></i></a>
              <a href=""><i class="fab fa-linkedin-in"></i></a>
            </div>
            <p>or use your account</p>
            <input
              type="email"
              placeholder="Email"
              name="Email"
              v-model="emailValue"
              id="emailValue"
            />
            <p class="error">{{ errorMessageSign1 }}</p>
            <input
              type="text"
              autocomplete="off"
              class="password"
              placeholder="Password"
              name="Pass"
              v-model="passValue"
              id="Pass"
            />
            <p class="error">{{ errorMessageSign2 }}</p>
            <p>Forget Your Password?</p>
            <button type="submit">Sign In</button>
          </form>
