</template>
<script>
export default {
  data() {
    return {
      nameVal: null,
      errorMessage: null,
      emailVal: null,
      errorMessageEmail: null,
      passVal: null,
      errorMessagePass: null,
      errorMessageSign1: null,
      emailValue: null,
      passValue: null,
      errorMessageSign2: null
    };
  },
  methods: {
    signUp() {
      document
        .getElementById("container")
        .classList.add("container-panel-active");
    },
    deleteSign() {
      document
        .getElementById("container")
        .classList.remove("container-panel-active");
    },
    validateForm() {
      if (!this.nameVal) {
        this.errorMessage = "the name is not found!!";
        document.getElementById("name").focus();
        return false;
      } else {
        this.errorMessage = null;
      }
      if (!this.emailVal) {
        this.errorMessageEmail = "the email is not found!!";
        document.getElementById("email").focus();
        return false;
      } else {
        this.errorMessageEmail = null;
      }
      if (!this.passVal) {
        this.errorMessagePass = "the password is not found!!";
        document.getElementById("pass").focus();
        return false;
      } else {
        this.errorMessagePass = null;
      }
      return true;
    },
    validateSignInForm() {
      if (!this.emailValue) {
        this.errorMessageSign1 = "the email is not found!!";
        document.getElementById("emailValue").focus();
        return false;
      } else {
        this.errorMessageSign1 = null;
      }
      if (!this.passValue) {
        this.errorMessageSign2 = "the Password is not found!!";
        document.getElementById("passValue").focus();
        return false;
      } else {
        this.errorMessageSign2 = null;
      }
    }
  }
};
</script>
